let __dropDown;
let __navigator;

function setDropDown(dropDown) {
  __dropDown = dropDown;
}

function getDropDown() {
  return __dropDown;
}

function setNavigator(navigator) {
  __navigator = navigator;
}

export const getNavigator = () => {
  return __navigator;
};

export const hideSplash = async () => {
  LogBox.ignoreLogs(['SplashScreen.show']);

  try {
    await SplashScreen.hideAsync();
  } catch (e) {
    console.log(`HideSplash error: ${e}`);
  }
};

// to use a global variable
let _Variables;

// Generric var-controlled log function
export const log = value => {
  if (_Variables && _Variables['logging'])
    console.log(`log: ${JSON.stringify(value)}`);
  return value;
};

// Delayed Navigation
export const delayNav = (props, delay, nav, screen) => {
  setTimeout(function () {
    if (nav === 'back') props.navigation.goBack();
    else props.navigation.navigate(nav, { screen: screen });
  }, delay);
};

export const PostResultToast = (
  fetchResult,
  screenName,
  nav = undefined,
  screen,
  delay,
  props
) => {
  // 'info' | 'warn' | 'error' | 'success'
  const timeout = 3000;

  function sleep(milliseconds) {
    var start = new Date().getTime();

    log(`sleeping for ${milliseconds} ms...`);
    for (var i = 0; i < 1e7; i++) {
      if (new Date().getTime() - start > milliseconds) {
        break;
      }
    }
    log(`Done`);
  }

  console.log(`fetchResult: ${JSON.stringify(fetchResult)}`);
  if (fetchResult?.error) {
    DropDownHolder.getDropDown().alertWithType(
      'error',
      screenName,
      'Error enviando la solicitud',
      null,
      timeout
    );
    log(`${screenName} POST error: ${JSON.stringify(fetchResult.error)}`);
  } else {
    DropDownHolder.getDropDown().alertWithType(
      'success',
      screenName,
      'Solicitud enviada con éxito',
      null,
      timeout
    );

    if (nav) {
      setTimeout(function () {
        if (nav === 'back') props.navigation.goBack();
        else props.navigation.navigate(nav, { screen: screen });
      }, delay);
    }
  }

  sleep(timeout);

  return Boolean(fetchResult?.error);
};

