// Convierte una fecha en formato YYYY-MM-DD a Date
export const IsoDateStringToDate = sDate => {
  const rslt = new Date(Date.parse(sDate));
  return rslt;
};

// Convierte una fecha en formato YYYY-MM-DD a Date
export const IsoDateStringToUTCDate = sDate => {
  var offset = new Date().getTimezoneOffset() * 60 * 1000;
  const rslt = new Date(Date.parse(sDate) + offset);
  return rslt;
};

// Convierte un Date a un string YYYY-MM-DD en zona UTC
export const DateToUtcString = aDate => {
  if (aDate) {
    // if(variables['logging']) console.log(
    // `Date2UTCString(${aDate}), type: ${typeof aDate}, ISO: ${aDate.toISOString()}, Local: ${aDate.toLocaleDateString()}`
    // );

    aDate.setHours(0);

    const UTCString = aDate.toISOString().split('T')[0];
    // if(variables['logging']) console.log(`UTCString: ${UTCString}`);
    return UTCString;
  }
  return '';
};

// Convierte un Date a un string YYYY-MM-DD
export const Date2IsoDateString = aDate => {
  // log(`Date2IsoDateString(${aDate})`);
  var offset = new Date().getTimezoneOffset() * 60 * 1000;
  const rslt = new Date(aDate - offset).toISOString().split('T')[0];
  // log(`Date2IsoDateString(${aDate}): ${rslt}`);
  return rslt;
};

// Convierte un Date a un string ISO YYYY-MM-DDTHH:mm:ss.sss-ZZZZ
export const Date2IsoDateTimeString = aDate => {
  // log(`Date2IsoDateTimeString(${aDate})`);
  var offset = new Date().getTimezoneOffset() * 60 * 1000;
  const rslt = new Date(aDate - offset).toISOString();
  // log(`Date2IsoDateTimeString(${aDate}): ${rslt}`);
  return rslt;
};

// Convierte un Date a un string User Friendly DD/MM/YY HH:MMM
export const Date2String = (aDate, format) => {
  // log(`Date2IsoDateTimeString(${aDate})`);
  const rslt = moment(new Date(aDate)).format(format);
  // log(`Date2IsoDateTimeString(${aDate}): ${rslt}`);
  return rslt;
};

// Convierte una fecha en formato Iso String a un string en el formato proporcionado
export const FormatIsoString = (sDate, format) => {
  const rslt = moment(sDate).format(format);
  // log(`FormatIsoString(${sDate}, ${format}): ${rslt}`);
  return rslt;
};

// Convierte una fecha en formato Iso String a un string en formato DD/MM
export const IsoString2ShortDate = utcString => {
  const aDate = new Date(utcString);
  const rslt = `${aDate.getDate()}/${1 + aDate.getMonth()}`;
  return rslt;
};

// Aritmetica de fechas
export const dateAdd = (aDate, days) => {
  const newDate = new Date(aDate.getTime() + days * 1000 * 60 * 60 * 24);
  if (_Variables['logging'])
    console.log(`dateSAdd(${aDate}, ${days}) => ${newDate}`);
  return newDate;
};

