export const DigicardScreenHeader = props => {
  const { theme, navigation, left, right, title } = props;
  const gvars = GlobalVariables.useValues();

  setNavigator(navigation);

  return (
    <View>
      <View
        style={title ? styles.containerOutterTall : styles.containerOutterShort}
        backgroundImageResizeMode="contain"
      >
        <View
          style={[
            styles.DigiHeader /*, { backgroundColor: theme.colors.light }*/,
          ]}
        >
          <View style={{ width: 25, marginLeft: 25 }}>
            {left ? (
              (left.onPress || left.nav) && (
                <IconButton
                  onPress={() => {
                    try {
                      left.onPress
                        ? left.onPress()
                        : navigation.navigate(left.nav, {
                            screen: left.screen,
                          });
                    } catch (err) {
                      console.error(err);
                    }
                  }}
                  icon={left.icon}
                  size={25}
                />
              )
            ) : (
              <IconButton
                onPress={() => {
                  try {
                    navigation.goBack();
                  } catch (err) {
                    console.error(err);
                  }
                }}
                icon={'AntDesign/arrowleft'}
                size={25}
              />
            )}
          </View>
          <Image
            style={{ width: '40%', height: '100%', maxHeight: 60 }}
            source={Images.DigiSSTransp}
            resizeMode={'contain'}
          />
          <View style={{ width: 25, marginRight: 25 }}>
            {right && (
              <IconButton
                onPress={() => {
                  try {
                    right.onPress
                      ? right.onPress()
                      : navigation.navigate(right.nav, {
                          screen: right.screen,
                        });
                  } catch (err) {
                    console.error(err);
                  }
                }}
                icon={right.icon}
                size={25}
              />
            )}
          </View>
        </View>

        {title && (
          <Text style={[styles.TextTitle, { color: theme.colors.strong }]}>
            {title}
          </Text>
        )}
      </View>

      {/* <DropdownAlert
        closeInterval={null}
        zIndex={10}
        ref={dropRef => {
          DropDownHolder.setDropDown(dropRef, title);
        }}
      /> */}

      <VariablesContainer vars={gvars} />
    </View>
  );
};

export const LoadingOverlay = props => {
  const Constants = GlobalVariables.useValues();
  const Variables = Constants;

  let { theme, message, action, visible } = props;
  let image;

  if (action === 'saving') {
    image = Images.Upload2562B92FB;
    if (!message) message = '\nGuardando';
  } else if (action === 'loading') {
    image = Images.Download2562B92FB;
    if (!message) message = '\nCargando';
  } else if (action === 'connecting') {
    image = Images.Wifi25655A4F5;
    if (!message) message = '\nConectando';
  } else {
    image = Images.Loader2562B92FB;
    if (!message) message = '';
  }

  // console.log(`Constants: ${JSON.stringify(Constants)}`);
  // visible={Constants['loading']}

  return (
    <Modal
      style={styles.OverlayModal}
      visible={visible}
      // visible={true}
      animationType={'fade'}
      transparent={true}
      presentationStyle={'overFullScreen'}
    >
      <BlurView style={styles.OverlayBlurView} tint={'dark'} intensity={10}>
        <View
          style={[
            action === 'connecting'
              ? styles.ImageViewConnecting
              : styles.ImageViewLoading,
            { backgroundColor: theme.colors.very_light, width: '70%' },
          ]}
          pointerEvents={'auto'}
        >
          <Image
            style={
              action === 'connecting'
                ? styles.ImageConnecting
                : styles.ImageLoading
            }
            source={image}
            resizeMode={'contain'}
          />
          <Touchable
            onPress={() => {
              try {
                setGlobalVariableValue({
                  key: 'loading',
                  value: false,
                });
              } catch (err) {
                console.error(err);
              }
            }}
          >
            <Text
              style={[
                action === 'connecting'
                  ? styles.OverlayTextConnecting
                  : styles.OverlayTextLoading,
                { color: theme.colors.secondary },
              ]}
            >
              {`Un momento por favor${message}...`}
            </Text>
          </Touchable>
        </View>
      </BlurView>
    </Modal>
  );
};

export const Registracion = props => {
  const { listData, dataSet, theme } = props;

  const concept = con_id => {
    // {con_id:1,con_name:"Regis",con_description:"Regis",con_type:"hs"}
    const found = dataSet?.find(e => e.con_id == con_id);

    return found?.con_name;
  };

  const UtcDate2ShortTime = utcDateStr => {
    return utcDateStr?.split('T')[1].substr(0, 5);
  };

  return (
    <>
      <Text
        style={{
          textAlign: 'right',
          color: theme.colors.strong,
          marginLeft: 0,
          marginRight: 5,
          width: 60,
        }}
      >
        {UtcDate2ShortTime(listData?.tir_in)}
      </Text>
      <Text
        style={{
          textAlign: 'right',
          color: theme.colors.strong,
          marginLeft: 0,
          marginRight: 5,
          width: 60,
        }}
      >
        {UtcDate2ShortTime(listData?.tir_out)}
      </Text>
      <Text
        style={{
          color: theme.colors.strong,
          marginLeft: 10,
          marginRight: 0,
          width: 150,
        }}
      >
        {concept(listData?.tir_concept_id)}
      </Text>
    </>
  );
};

